import React, { memo } from 'react';
import { Spinner } from 'shared/Spinner';
import { Text, TextSize } from 'core/src/typography';
import { Button } from 'core/src/Button';
import { Sizes } from 'shared/Spinner/types';
import { SizeVariant } from 'core/types/common';

import MiddleOverflowText from '../../MiddleOverflowText';

import styles from './styles.scss';

interface LoadingScreenProps {
  /** Текст при загрузке */
  loadingText?: string;

  /** Имя файла */
  fileName?: string;

  /* Размер спиннера */
  loaderSize?: Sizes;

  /** Размер текста */
  textSize?: TextSize;

  /** Размер кнопки Отмены */
  buttonSize?: SizeVariant;

  /** Обработчик отмены загрузки */
  onCancel?: VoidFunction;
}

/** Экран загрузки файла */
function LoadingScreen({
  loaderSize,
  textSize,
  loadingText,
  fileName,
  buttonSize,
  onCancel
}: LoadingScreenProps): JSX.Element {
  return (
    <div className={ styles.wrapper_loading }>
      <div className={ styles.loading_top }>
        <Spinner
          color="light"
          size={ loaderSize }
        />
        <div className={ styles.loading_text }>
          <MiddleOverflowText
            size={ textSize }
            color="primary"
            value={ fileName || '' }
          />

          <Text size={ textSize } color="tertiary">{ loadingText }</Text>
        </div>
      </div>
      <div className={ styles.loading_button }>
        <Button
          text="Отмена"
          size={ buttonSize }
          type="secondary"
          onClick={ onCancel }
        />
      </div>
    </div>
  );
}

const MemoLoadingScreen = memo(LoadingScreen);
MemoLoadingScreen.displayName = 'LoadingScreen';

export default MemoLoadingScreen;
